<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <div class="goodscart-item">
            <div class="order-loop">
              <div class="order-con">
                <div class="goods-img" @click="imgPreview(Detail.picture?Detail.picture:defaultImg)"><img :src="Detail.picture" :onerror="defaultImg"></div>
                <div class="goods-name">
                  <div class="name" @click="toProductDetail(Detail.product_id,Detail.product_name)">{{ Detail.product_name }}</div>
                  <!-- <van-tag plain type="primary">{{ Detail.amount }}</van-tag> -->
                </div>
                <div class="goods-price">
                  <div class="jiage">¥{{ Detail.price }}</div>
                  <div class="shuliang">× {{ Detail.total }}</div>
                </div>
              </div>
              <!-- <div v-if="spxx.accessorList.length>0 && spxx.check" class="common-more" @click="showMore1(index,indexs)">
                  <div class="more">
                    <em>隐藏</em>
                  </div>
                </div>
                <div v-if="spxx.accessorList.length>0 && !spxx.check" class="common-more" @click="showMore1(index,indexs)">
                  <div class="more">
                    <em>显示</em>
                  </div>
                </div>
                <div v-if="spxx.check" class="accessories">
                  <div v-for="(item1,index1) of spxx.accessorList" :key="index1" class="accessories-list">
                    <div class="accessories-tit">{{ item1.name }}</div>
                    <div class="accessories-info">¥{{ item1.price }}</div>
                  </div>
                </div> -->
            </div>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <div class="evaluation-content">
            <div class="page-list">
              <div class="pagelist-left"><span style="color:red">*</span> 商品评分</div>
              <div class="pagelist-right left"><van-rate v-model="starvalue" /></div>
            </div>
            <div class="page-list block">
              <div class="pagelist-left"><span style="color:red">*</span> 评价内容</div>
              <div class="pagelist-right">
                <van-field
                  v-model="message"
                  rows="3"
                  autosize
                  type="textarea"
                  placeholder="请输入评价内容"
                  maxlength="250"
                />
              </div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">附件</div>
              <div class="pagelist-right left" style="margin-left: 0">
                <XyUploadPlus
                  ref="uploadEl"
                  style="width:100%;"
                  :filters="{ext:'jpg,jpeg,png'}"
                  :filegroup="filegroup"
                  filed="test1"
                  @getFileChangeInfo="getFileChangeInfo"
                />
              </div>
            </div>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="saveOrderEvaluateAction">保存评价</van-button></li>
      </ul>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { ImagePreview } from 'vant'
export default {
  name: 'OrderEvaluation',
  components: {
    headerBar,
    XyUploadPlus
  },
  mixins: [pageRouter],
  data() {
    return {
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      filegroup: '',
      fileChangeInfo: {},
      message: '',
      checked: true,
      starvalue: 0,
      fileList: [],
      headerData: {
        title: '评价晒单',
        leftUrl: 'back',
        leftText: ' '
      },
      detailId: '',
      Detail: '',
      Order: '',
      place_time: ''
    }
  },
  created() {
    this.detailId = this.$store.state.member.orderId
    this.getData()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    // 保存评价
    saveOrderEvaluateAction() {
      if (!this.starvalue) {
        this.$toast('请进行商品评分')
        return
      }
      if (!this.message) {
        this.$toast('请输入评价内容')
        return
      }
      const pre = {
        id: this.Detail.id,
        fileChangeInfo: this.fileChangeInfo,
        evaluation_star: this.starvalue,
        evaluation_desc: this.message
      }
      this.$store
        .dispatch('member/saveOrderDetailEvaluationAction', pre)
        .then(res => {
          this.$toast('保存评价成功')
          this.$bus.$emit('pjSearch')
          this.$router.push('MyOrder')
        })
        .catch(() => {
          this.loading = false
        })
    },
    getData() {
      this.$store
        .dispatch('member/getOrderDetailAction', { e_order_detail_id: this.detailId })
        .then(res => {
          this.Detail = res.e_order_detail
          this.Order = res.e_order
          this.place_time = res.place_time
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 上传附件回调函数 name:附件名称，fileChanInfo:附件信息
    getFileChangeInfo(name, fileChangeInfo) {
      this.fileChangeInfo[name] = fileChangeInfo
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd'
      })
    }
  }
}
</script>

<style lang="css" scoped>

</style>
